import React, { useEffect, useState } from "react";
import { Link, useParams } from "react-router-dom";
import { useTranslation } from "react-i18next";
import Box from "@mui/material/Box";
import Typography from "@mui/material/Typography";
import Table from "@mui/material/Table";
import TableBody from "@mui/material/TableBody";
import TableCell from "@mui/material/TableCell";
import TableContainer from "@mui/material/TableContainer";
import TableHead from "@mui/material/TableHead";
import TableRow from "@mui/material/TableRow";
import Paper from "@mui/material/Paper";
import {
  CircularProgress,
  FormControlLabel,
  MenuItem,
  Select,
  Switch,
  TablePagination,
  IconButton,
  Button,
} from "@mui/material";
import DeleteIcon from "@mui/icons-material/Delete";
import EventIcon from "@mui/icons-material/Event";
import { Header } from "~/components/manage/Header";
import SurveyService from "~/services/SurveyService";
import ProcessedError from "~/components/manage/ProcessedError";
import { PROCESSED_ERRORS, processError } from "~/utils/errorsProcessor";
import styles from "./ResponsesSurvey.module.css";
import {
  formatlocalDateTime,
  serverDateTimeToLocalDateTime,
} from "~/utils/DateUtils";
import { previewUrl } from "~/networking/run";
import { ResponseDelete } from "~/components/manage/ResponseDelete";
import FileSaver from 'file-saver';

function ResponsesSurvey() {
  const { t } = useTranslation("manage");
  const [fetching, setFetching] = useState(true);

  const [dbResponses, setDbResponses] = useState(false);
  const [completeResponses, setCompleteResponses] = useState("none");
  const [previewResponses, setPreviewResponses] = useState("none");
  const [processedError, setProcessedError] = useState("");
  const [showError, setShowError] = useState(false);

  const { surveyId } = useParams();

  const processApirror = (e) => {
    setFetching(false);
    const processed = processError(e);
    switch (processed) {
      case PROCESSED_ERRORS.NETWORK_ERR:
      case PROCESSED_ERRORS.BACKEND_DOWN:
        setShowError(true);
        setProcessedError(processed);
        break;
      default:
        break;
    }
  };

  const [allResponse, setAllResponse] = useState(null);

  const [page, setPage] = useState(1);
  const [rowsPerPage, setRowsPerPage] = useState(10);

  const exportResponses = () => {
    setFetching(true);
    SurveyService.exportResponses(
      surveyId,
      dbResponses,
      completeResponses,
      previewResponses
    )
      .then((data) => {
        if (data) {
          var file = new File([data], `${surveyId}-responses-export.csv`, {type: "text/csv;charset=utf-8"});
          FileSaver.saveAs(file);
        }
        setFetching(false);
      })
      .catch((err) => {
        processApirror(err);
      });
  };

  const fetchResponses = () => {
    setFetching(true);
    SurveyService.allResponse(
      surveyId,
      dbResponses,
      page,
      rowsPerPage,
      completeResponses,
      previewResponses
    )
      .then((data) => {
        if (data) {
          setAllResponse(data);
        }
        setFetching(false);
      })
      .catch((err) => {
        processApirror(err);
      });
  };

  useEffect(() => {
    fetchResponses();
  }, [page, rowsPerPage, completeResponses, previewResponses]);

  const [responseToDelete, setResponseToDelete] = useState();
  const onCloseModal = () => {
    setResponseToDelete(null);
  };

  const deleteResponse = () => {
    onCloseModal();
    SurveyService.deleteResponse(surveyId, responseToDelete.id)
      .then(() => {
        fetchResponses();
      })
      .catch((e) => {
        processApirror(e);
      });
  };

  return (
    <Box className={styles.mainContainer}>
      {showError && (
        <ProcessedError
          error={processedError}
          handleClose={() => setShowError(false)}
        />
      )}
      <Header />
      <Typography variant="h4" sx={{ mb: "10px" }}>
        {t("responses.title")}
      </Typography>

      <FormControlLabel
        value="start"
        labelPlacement="start"
        control={
          <Switch
            checked={dbResponses}
            onChange={(event) => {
              setDbResponses(event.target.checked);
              fetchResponses();
            }}
          />
        }
        label={t("responses.raw_values")}
      />
      <br />
      <FormControlLabel
        labelPlacement="start"
        control={
          <Select
            sx={{ margin: "8px" }}
            variant="standard"
            value={completeResponses}
            onChange={(event) => {
              setPage(1);
              setCompleteResponses(event.target.value);
            }}
          >
            <MenuItem value="none">
              {t("responses.filter_completed_show_all")}
            </MenuItem>
            <MenuItem value={true}>
              {t("responses.filter_completed_show_completed")}
            </MenuItem>
            <MenuItem value={false}>
              {t("responses.filter_completed_show_incomplete")}
            </MenuItem>
          </Select>
        }
        label={t("responses.filter_completed")}
      />
      <br />
      <FormControlLabel
        labelPlacement="start"
        control={
          <Select
            sx={{ margin: "8px" }}
            variant="standard"
            value={previewResponses}
            onChange={(event) => {
              setPage(1);
              setPreviewResponses(event.target.value);
            }}
          >
            <MenuItem value="none">
              {t("responses.filter_preview_show_all")}
            </MenuItem>
            <MenuItem value={true}>
              {t("responses.filter_preview_show_preview")}
            </MenuItem>
            <MenuItem value={false}>
              {t("responses.filter_preview_show_actual")}
            </MenuItem>
          </Select>
        }
        label={t("responses.filter_preview")}
      />
      <br />
      <Button sx={{ margin: "8px" }} onClick={() => exportResponses()}>Export Responses</Button>
      {fetching ? (
        <div className={styles.loadingWrapper}>
          <CircularProgress />
        </div>
      ) : (
        <Paper sx={{ width: "100%", mb: 2 }}>
          <TableContainer component={Paper}>
            <Table sx={{ minWidth: 650 }} aria-label="simple table">
              <TableHead>
                {allResponse && (
                  <TableRow>
                    <TableCell key="btns" />
                    <TableCell key="id" align="left">
                      ID
                    </TableCell>
                    <TableCell key="preview" align="left">
                      {t("responses.preview")}
                    </TableCell>
                    <TableCell key="version" align="left">
                      {t("responses.version")}
                    </TableCell>
                    <TableCell key="ip" align="left">
                      {t("responses.ip_addr")}
                    </TableCell>
                    <TableCell key="start_date" align="left">
                      {t("responses.start_date")}
                    </TableCell>
                    <TableCell key="submit_date" align="left">
                      {t("responses.submit_date")}
                    </TableCell>
                    <TableCell key="lang" align="left">
                      {t("responses.lang")}
                    </TableCell>

                    {Object.values(allResponse?.columnNames).map((label) => {
                      return (
                        <TableCell key={label} align="left">
                          {label}
                        </TableCell>
                      );
                    })}
                  </TableRow>
                )}
              </TableHead>
              <TableBody>
                {allResponse?.responses.map((response) => {
                  return (
                    <TableRow key={response.id} sx={{ minHeight: "100px" }}>
                      <TableCell key="btns" align="left">
                        <Box sx={{ display: "flex" }}>
                          <IconButton
                            onClick={() => {
                              setResponseToDelete(response);
                            }}
                          >
                            <DeleteIcon />
                          </IconButton>
                          {response.hasEvents && (
                            <Link
                              to={`/response-events/${surveyId}/${response.id}`}
                            >
                              <IconButton>
                                <EventIcon />
                              </IconButton>
                            </Link>
                          )}
                        </Box>
                      </TableCell>
                      <TableCell key="id" align="left">
                        {response.id}
                      </TableCell>
                      <TableCell key="preview" align="left">
                        {JSON.stringify(response.preview)}
                      </TableCell>
                      <TableCell key="version" align="left">
                        {response.version}
                      </TableCell>
                      <TableCell key="ip" align="left">
                        {response.ipAddress}
                      </TableCell>
                      <TableCell key="startDate" align="left">
                        {formatlocalDateTime(
                          serverDateTimeToLocalDateTime(response.startDate)
                        )}
                      </TableCell>
                      <TableCell key="submitDate" align="left">
                        {response.submitDate
                          ? formatlocalDateTime(
                              serverDateTimeToLocalDateTime(response.submitDate)
                            )
                          : " - "}
                      </TableCell>
                      <TableCell key="lang" align="left">
                        {response.lang.code}
                      </TableCell>
                      {response.values.map((value, index) => {
                        return (
                          <TableCell align="left" key={index}>
                            {value === null ||
                            value === undefined ||
                            value === "" ? (
                              " - "
                            ) : typeof value === "string" ? (
                              value
                            ) : typeof value === "object" &&
                              "size" in value &&
                              "filename" in value &&
                              "stored_filename" in value ? (
                              <a
                                target="_blank"
                                download={value.stored_filename}
                                href={previewUrl(value.stored_filename)}
                              >
                                {value.filename} -
                                {Math.round(value.size / 1000) + "K"}
                              </a>
                            ) : (
                              JSON.stringify(value)
                            )}
                          </TableCell>
                        );
                      })}
                    </TableRow>
                  );
                })}
              </TableBody>
            </Table>
          </TableContainer>
          <TablePagination
            rowsPerPageOptions={[10, 25, 50, 100]}
            component="div"
            labelDisplayedRows={({ from, to, count, page }) => {
              return t("responses.label_displayed_rows", { from, to, count });
            }}
            labelRowsPerPage={t("responses.label_rows_per_page")}
            count={allResponse?.totalCount}
            rowsPerPage={rowsPerPage}
            page={page - 1}
            onPageChange={(event, newPage) => {
              setPage(newPage + 1);
            }}
            onRowsPerPageChange={(event) => {
              setRowsPerPage(parseInt(event.target.value, 10));
              setPage(1);
            }}
          />
        </Paper>
      )}
      {Boolean(responseToDelete) && (
        <ResponseDelete
          onDelete={deleteResponse}
          open={Boolean(responseToDelete)}
          onClose={onCloseModal}
        />
      )}
    </Box>
  );
}

export default ResponsesSurvey;
